.Header {
  padding-top: 20px;
}
.Title {
  margin-bottom: 0 !important;
  color: #000 !important;
}
.Body {
  display: flex;
  display: -webkit-flex;
  /* Safari 8 */
  flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  /* Safari 8 */
}
.Header {
  padding-top: 10px;
}
.Title {
  width: calc(100% - 95px);
}
.Title h1,
.DetailsWithAvatar h2 {
  margin-bottom: 0;
}
.Title > div,
.DetailsWithAvatar > div {
  font-size: 17px;
}
.CheckBox {
  float: left;
  line-height: 2.4;
  margin-right: 20px;
}
.BlurContent {
  -webkit-filter: url(#svg-blur);
  filter: url(#svg-blur);
  pointer-events: none;
}
